.menu__icon {
  /*  display: none;*/
  width: 30px;
  height: 22px;
  position: relative;
  cursor: pointer; }
  .menu__icon span {
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    background: #47aa22;
    /* border-radius: 9px;*/
    opacity: 1;
    left: 0;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    transition: .25s ease-in-out; }
    .menu__icon span:nth-of-type(1) {
      top: 0px; }
    .menu__icon span:nth-of-type(2) {
      top: 10px; }
    .menu__icon span:nth-of-type(3) {
      top: 10px; }
    .menu__icon span:nth-of-type(4) {
      top: 20px; }

.menu__icon.open span:nth-of-type(1) {
  top: 18px;
  width: 0%;
  left: 50%; }

.menu__icon.open span:nth-of-type(2) {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg); }

.menu__icon.open span:nth-of-type(3) {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg); }

.menu__icon.open span:nth-of-type(4) {
  top: 18px;
  width: 0%;
  left: 50%; }

